import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

export type TMarketColorPickerInputTemplateArgs = Partial<HTMLMarketColorPickerInputElement> & {
  label?: string;
};

export const ColorPickerInputTemplate = ({ value, label }: TMarketColorPickerInputTemplateArgs) => html`
  <market-color-picker-input value=${ifDefined(value)}>
    ${label ? html`<label slot="label">${label}</label>` : ''}
  </market-color-picker-input>
`;
